.centerXY {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

ul {
  position: relative;
  transform: skewY(-15deg);
}

ul li {
  width: 70px;
  z-index: var(--i);
  background: #8f317f;
  padding: 15px;
  list-style: none;
  transition: all .5s;
  position: relative;
}

ul li:hover {
  background: #33a3ee;
  transform: translateX(-50px);
}

ul li:before {
  content: "";
  width: 40px;
  height: 100%;
  transform-origin: 100%;
  background: #6e1a60;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: -40px;
  transform: skewY(45deg);
}

ul li:hover:before {
  background: #2d6f9e;
}

ul li:after {
  content: "";
  width: 100%;
  height: 40px;
  transform-origin: bottom;
  background: #a33991;
  transition: all .5s;
  position: absolute;
  top: -40px;
  left: 0;
  transform: skewX(45deg);
}

ul li:hover:after {
  background: #2982b9;
}

ul li a {
  color: #1d1d1d;
  font-size: 10px;
  text-decoration: none;
  transition: all .5s;
  display: block;
}

ul li:hover a {
  color: #000;
}

ul li:last-child:after {
  box-shadow: -120px 80px 20px #00000040;
}

